body {
  background-color: gray;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #333;
  text-align: center;
  background-color: #ccc;
  padding: 10px 0;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

nav ul a {
  color: #333;
  margin: 0 10px;
  text-decoration: none;
}

.container {
  background-color: #fffc;
  border-radius: 23px;
  max-width: 800px;
  margin: 20px auto;
  padding: 0 20px;
}

h2 {
  margin-top: 0;
}

#cart-items {
  border: 1px solid #ccc;
  margin-bottom: 10px;
  padding: 10px;
}

#cart-total {
  text-align: right;
}

footer {
  color: #333;
  text-align: center;
  background-color: #ccc;
  width: 100%;
  padding: 10px 0;
  position: fixed;
  bottom: 0;
}

